<template lang="pug">
  v-app(dark v-scroll="onScroll")
    toolbar(
      :toolbar-light="toolbarLight"
      :toolbar-style="toolbarStyle"
      )
    v-parallax(
      src="static/img002.jpg"
      height="800"
      )
      v-layout.cover__content(column align-center justify-center)
        v-avatar.elevation-12(size="180")
          img(
            src="https://pt.gravatar.com/userimage/135333990/4f6d8aa6be9d5cfde478c9571e8dd7e9.jpeg?size=200"
            alt="Profile picture"
            )
        .white--text.mt-5.text-xs-center
          .display-2.font-weight-regular HERICK FERNANDO
          v-chip.title.font-weight-medium.blue--text.lighten-1 SOFTWARE WEB DEVELOPER
    about
    v-divider
    education
    v-divider
    experience
    v-divider
    skill
</template>

<script>
export default {
  components: {
    Toolbar: () => import('@/components/Toolbar'),
    About: () => import('@/App/About'),
    Education: () => import('@/App/Education'),
    Experience: () => import('@/App/Experience'),
    Skill: () => import('@/App/Skill'),
  },
  data: () => ({
    toolbarLight: true,
    toolbarStyle: {
      'transparent elevation-0': true,
    },
  }),
  methods: {
    onScroll() {
      const offsetTop = window.pageYOffset || document.documentElement.scrollTop;
      this.toolbarLight = offsetTop === 0;
      this.toolbarStyle = { 'transparent elevation-0': offsetTop === 0 };
    },
  },
};
</script>

<style lang="stylus">
  .theme--light.v-toolbar
    background-color transparent !important
</style>



